.spotify-player {
    position: relative;
    z-index: 1;
    margin: 20px auto;
    width: 100%;
    max-width: 1200px;
    padding: 0 30px;
    box-sizing: border-box;
    height: calc(100vh - 40px);
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: #1DB954 #282828;
    background: rgba(25, 20, 20, 0.8); /* Transparan */
    backdrop-filter: blur(5px); /* Opsional untuk efek blur */
}

/* Custom scrollbar styling */
.spotify-player::-webkit-scrollbar {
    width: 8px;
    position: absolute;
    right: 0;
}

.spotify-player::-webkit-scrollbar-track {
    background: #282828;
    border-radius: 4px;
}

.spotify-player::-webkit-scrollbar-thumb {
    background: #1DB954;
    border-radius: 4px;
}

.spotify-player::-webkit-scrollbar-thumb:hover {
    background: #1ed760;
}

.polygon-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1; /* Pastikan partikel ada di bawah */
    overflow: hidden;
}

.polygon {
    position: absolute;
    width: 100px;
    height: 100px;
    background: rgba(0, 123, 255, 0.5); /* Transparan sebagian */
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
    animation: movePolygon 20s infinite linear;
}

.polygon:nth-child(1) {
    top: 10%;
    left: 20%;
    animation-duration: 25s;
}

.polygon:nth-child(2) {
    top: 30%;
    left: 70%;
    animation-duration: 20s;
}

.polygon:nth-child(3) {
    top: 50%;
    left: 40%;
    animation-duration: 30s;
}

.polygon:nth-child(4) {
    top: 70%;
    left: 80%;
    animation-duration: 35s;
}

.polygon:nth-child(5) {
    top: 90%;
    left: 10%;
    animation-duration: 40s;
}

@keyframes movePolygon {
    0% {
        transform: translate(0, 0) scale(1);
    }
    50% {
        transform: translate(50vw, 50vh) scale(1.5);
    }
    100% {
        transform: translate(0, 0) scale(1);
    }
}

.search-form {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

.search-input {
    flex: 1;
    padding: 12px;
    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.3);
    color: white;
    font-size: 16px;
}

.search-button, .control-button {
    padding: 12px 24px;
    font-size: 14px;
    background-color: #1DB954;
    color: #fff;
    border: none;
    border-radius: 50px; /* Rounded like Spotify buttons */
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.search-button:hover, .control-button:hover {
    background-color: #1ED760;
    transform: translateY(-2px);
}

.spotify-player {
    background-color: #191414; /* Spotify black */
    color: white;
    font-family: 'Circular', 'Helvetica Neue', 'Arial', sans-serif;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

.suggestions {
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(10px);
    border-radius: 5px;
    padding: 10px;
    margin-top: 10px;
}

.suggestions ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: grid;
    gap: 10px;
}

.suggestions li {
    cursor: pointer;
    padding: 5px 0;
    background-color: rgba(0, 0, 0, 0.5); /* Background for text */
    color: white; /* Text color */
    border-radius: 5px;
    margin-bottom: 5px;
}

.suggestions li:hover {
    background-color: rgba(0, 0, 0, 0.7);
}

.track-details {
    width: 100%;
    max-width: none;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(5px);
    padding: 30px;
    border-radius: 15px;
    margin: 20px 0;
}

.track-info {
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 30px;
    margin-bottom: 30px;
}

.album-art {
    width: 300px;
    height: 300px;
    border-radius: 12px;
    object-fit: cover;
}

.track-meta {
    text-align: left;
    color: white;
}

.track-meta h2 {
    margin: 0;
    font-size: 24px;
    color: #fff;
    font-size: 2em;
    margin-bottom: 15px;
}

.track-meta p {
    margin: 5px 0;
    color: #b3b3b3;
    margin: 8px 0;
}

.player-controls {
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.control-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #1DB954;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 10px;
}

.control-button:hover {
    background-color: #1ED760;
}

.seek-bar {
    width: 100%;
    max-width: 300px;
    margin-left: 10px;
}

.lyrics {
    margin-top: 20px;
    text-align: left;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.2); /* Simple transparent background */
    backdrop-filter: blur(5px); /* Slight blur effect */
    padding: 10px;
    border-radius: 5px;
    max-height: 300px;
    overflow-y: auto; /* Allow vertical scrolling */
    background: rgba(0, 0, 0, 0.3);
    padding: 20px;
    border-radius: 12px;
    margin-top: 20px;
}

.lyrics h3 {
    margin-bottom: 10px;
}

.lyrics div {
    white-space: pre-wrap;
    word-wrap: break-word;
}

.device-status {
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    gap: 10px;
}

.reconnect-button {
    padding: 5px 10px;
    background-color: #1DB954;
    color: white;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.reconnect-button:disabled {
    background-color: #666;
    cursor: not-allowed;
    opacity: 0.7;
}

.reconnect-button:hover:not(:disabled) {
    background-color: #1ed760;
    transform: scale(1.05);
}

@keyframes gradientAnimation {
    0% { background-position: left center; }
    100% { background-position: right center; }
}

@media (max-width: 1200px) {
    .spotify-player {
        max-width: 900px;
        padding: 0 20px;
    }

    .track-details,
    .spotify-embed {
        max-width: 800px;
    }

    .album-art {
        max-width: 200px;
    }
}

@media (max-width: 768px) {
    .spotify-player {
        max-width: 100%;
        padding: 0 15px;
        margin: 10px 0;
    }

    .track-details,
    .spotify-embed {
        max-width: 100%;
    }

    .track-info {
        gap: 15px;
    }

    .album-art {
        max-width: 150px;
    }

    .spotify-embed iframe {
        min-height: 152px;
    }
}

@media (max-width: 480px) {
    .spotify-player {
        padding: 0 10px;
    }

    .search-input {
        font-size: 12px;
    }

    .search-button {
        font-size: 12px;
    }

    .album-art {
        max-width: 120px;
        margin-right: 0;
    }

    .control-button {
        font-size: 12px;
        padding: 6px 12px;
    }

    .seek-bar {
        font-size: 12px;
        padding: 3px;
    }

    .track-info {
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: 10px;
    }

    .track-meta {
        text-align: center;
    }
}

.spotify-embed {
    width: 100%;
    margin: 20px 0;
    border-radius: 12px;
    overflow: hidden;
    background: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(5px);
}

.spotify-embed iframe {
    display: block;
    border-radius: 12px;
    min-height: 180px;
}

.spotify-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  width: 100%;
  padding: 20px;
  background: linear-gradient(135deg, #1a1a1a 0%, #0a0a0a 100%);
  position: relative;
  overflow-y: auto;
  margin-bottom: 100px; /* Add extra margin at bottom */
}

.spotify-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  transition: transform 0.1s ease-out;
}

.spotify-polygon {
  position: absolute;
  width: 150px;
  height: 150px;
  background: rgba(29, 185, 84, 0.1);
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  animation: movePolygon 20s infinite linear;
}

.spotify-polygon:nth-child(1) { top: 10%; left: 10%; animation-duration: 25s; }
.spotify-polygon:nth-child(2) { top: 60%; left: 80%; animation-duration: 30s; }
.spotify-polygon:nth-child(3) { top: 80%; left: 20%; animation-duration: 35s; }

.spotify-card {
  position: relative;
  z-index: 2; /* Above particles */
  width: 100%;
  max-width: 1400px; /* Match Profile.css */
  margin: 20px auto; /* Match Profile.css */
  display: grid;
  grid-template-columns: 1fr 2fr; /* Changed to make search column bigger */
  gap: 40px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 50px; /* Match Profile.css */
  min-height: calc(100vh - 40px);
}

.spotify-left {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  min-width: 300px; /* Minimum width for search column */
}

.spotify-right {
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 100%;
}

.album-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.album-item {
  width: 150px;
  height: 150px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.album-item:hover {
  transform: scale(1.05);
}

.album-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Responsive adjustments */
@media (min-width: 1800px) {
  .spotify-card {
    max-width: 1800px;
    grid-template-columns: 1fr 2fr;
  }
}

@media (min-width: 2000px) {
  .spotify-card {
    max-width: 2000px;
  }
}

@media (max-width: 1200px) {
  .spotify-card {
    grid-template-columns: 1fr 1.5fr;
  }
}

@media (max-width: 992px) {
  .spotify-card {
    grid-template-columns: 1fr;
    max-width: 100%;
  }
  
  .spotify-left {
    min-width: 100%;
  }
}

@media (max-width: 1600px) {
  .spotify-card {
    max-width: 1400px;
    grid-template-columns: minmax(300px, 400px) 1fr;
    padding: 40px;
  }
}

@media (max-width: 1400px) {
  .spotify-card {
    grid-template-columns: minmax(250px, 300px) minmax(400px, 1fr);
  }
}

@media (max-width: 1200px) {
  .spotify-card {
    max-width: 1000px;
    grid-template-columns: 300px 1fr;
    padding: 30px;
  }

  .track-details,
  .spotify-embed {
    max-width: 800px;
  }

  .album-art {
    width: 180px;
    height: 180px;
  }
}

@media (max-width: 992px) {
  .spotify-card {
    grid-template-columns: 1fr;
    max-width: 90vw;
  }

  .spotify-left,
  .spotify-right {
    width: 100%;
  }

  .album-art {
    width: 250px;
    height: 250px;
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .spotify-container {
    padding: 10px;
  }

  .spotify-card {
    padding: 20px;
    margin: 10px;
  }

  .album-art {
    width: 200px;
    height: 200px;
  }
}

@media (max-width: 480px) {
  .spotify-card {
    margin: 10px;
    padding: 15px;
  }

  .album-art {
    width: 150px;
    height: 150px;
  }

  .spotify-embed iframe {
    height: 152px;
  }
}

.suggestion-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  background: rgba(255, 255, 255, 0.1);
  border-left: 4px solid #1DB954;
  border-radius: 8px;
  transition: all 0.3s ease;
  cursor: pointer;
}

.suggestion-item:hover {
  background: rgba(29, 185, 84, 0.2);
  transform: translateX(5px);
  border-left-width: 8px;
}

.suggestion-thumbnail {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  object-fit: cover;
}

.suggestion-info {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.suggestion-info strong {
  color: #fff;
  font-size: 1em;
}

.suggestion-info span {
  color: #b3b3b3;
  font-size: 0.9em;
}
