.home-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%;
    padding: 20px;
    position: relative;
    overflow: hidden;
}

.home-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.home-card {
    position: relative;
    z-index: 2; /* Ensure card is above particles */
    width: 100%;
    max-width: 1800px; /* Increased from 1200px */
    margin: 0 auto;
    display: grid;
    grid-template-columns: minmax(400px, 500px) 1fr; /* Wider left column */
    gap: 50px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    padding: 50px;
}

.home-left {
    text-align: center;
}

.home-right {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.welcome-section,
.navigation-section,
.featured-section {
    background: rgba(0, 0, 0, 0.3);
    padding: 20px;
    border-radius: 15px;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    gap: 10px;
    margin-top: 15px;
}

.skill-tag {
    background: rgba(29, 185, 84, 0.2);
    padding: 8px 15px;
    border-radius: 20px;
    text-align: center;
}

.home-content {
    text-align: center;
    color: white;
}

.home-image {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 4px solid #1DB954;
    padding: 4px;
    margin-bottom: 20px;
    transition: transform 0.3s ease;
}

.home-image:hover {
    transform: scale(1.05);
}

.home-content h1 {
    font-size: 2.5em;
    color: #1DB954;
    margin-bottom: 10px;
}

.tagline {
    font-size: 1.2em;
    color: #b3b3b3;
    margin-bottom: 30px;
}

.welcome-text {
    max-width: 600px;
    margin: 0 auto 40px;
    line-height: 1.6;
}

.quick-links {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    max-width: 800px;
    margin: 0 auto;
}

.quick-link-button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 15px;
    background: rgba(29, 185, 84, 0.2);
    border-radius: 10px;
    text-decoration: none;
    color: white;
    transition: all 0.3s ease;
}

.quick-link-button:hover {
    background: rgba(29, 185, 84, 0.4);
    transform: translateY(-3px);
}

/* Responsive Design */
@media (max-width: 1800px) {
    .home-card {
        max-width: 1600px;
        grid-template-columns: minmax(350px, 450px) 1fr;
    }
}

@media (max-width: 1600px) {
    .home-card {
        max-width: 1400px;
        grid-template-columns: minmax(300px, 400px) 1fr;
    }
}

@media (max-width: 1200px) {
    .home-card {
        max-width: 1000px;
        grid-template-columns: 300px 1fr;
    }
}

@media (max-width: 992px) {
    .home-card {
        grid-template-columns: 1fr;
        max-width: 800px;
    }
}

@media (max-width: 768px) {
    .home-card {
        grid-template-columns: 1fr;
        max-width: 600px;
        padding: 20px;
    }

    .home-left {
        margin-bottom: 20px;
    }

    .home-image {
        width: 150px;
        height: 150px;
    }

    .home-content h1 {
        font-size: 2em;
    }

    .quick-links {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 480px) {
    .home-card {
        margin: 10px;
        padding: 15px;
    }

    .quick-links {
        grid-template-columns: 1fr;
    }

    .home-image {
        width: 120px;
        height: 120px;
    }

    .home-content h1 {
        font-size: 1.5em;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
